.skillsBlock {
    background-color: #c0ffc8;
    min-height: 100vh;
    display: flex;
    flex-direction: column;

}
.title {
    min-height: 5vh;
    border: 1px solid;
    display: flex;
    align-content: center;
    justify-content: center;
    align-items: center;
}
title,h2 {
    margin: 0;
}

.skills {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    flex-wrap: wrap;

}